.pages {
    max-width: 100%;
    display: flex;
    justify-content: center;
}

.page {
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    color: #000;
    cursor: pointer;

}
.selectedPage {
    font-weight: bold;
}

.paginatorBtn {
    background-color: firebrick;
    color: white;
    border: none;
    cursor: pointer;
    width: 50px;
}
.paginatorBtn:active {
     border: 1px solid black;
 }