.userIcon {
    width: 75px;
    height: 75px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 10px;
}

.usersBtn {
    width: 100px;
    height: 40px;
    text-align: center;
    background-color: firebrick;
    border: none;
    color: #fff;
    cursor: pointer;
}

.userContainer {
    display: flex;
    border: 1px solid firebrick;
    justify-content: space-between;
    padding: 15px;
    align-items: center;
    width: 100%;
    margin-left: 30px;
}

.userInner {
    padding: 10px;
    border-top: 2px solid firebrick;
    margin-top: 20px;
    display: flex;
    align-items: flex-end;
}

.userName {
    font-weight: bold;
    font-size: 24px;
}

.userCountry {
    font-weight: bold;
}

.column {
    display: flex;
    flex-direction: column;
    align-items: center;
}



